import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import { getResponseMessage} from "./selectors";

import {
    SEND_RESPONSE
} from './constants';

import { Widget, addResponseMessage } from 'react-chat-widget';
import 'react-chat-widget/lib/styles.css';

import logo from './pictures/logo-avanti.png';

class ComponentAuth extends Component {

    render() {
        const {
            sendResponse
        } = this.props;

        return (
                <div className="chat-demo-wrapper">
                    <Widget
                        title='Chat Avanti'
                        subtitle=''
                        senderPlaceHolder='Escribe un mensaje...'
                        handleNewUserMessage={sendResponse}
                        profileAvatar={logo}
                    />
                </div>
            );
        }
}

const mapStateToProps = state => {
    return {
        responseMessage: getResponseMessage(state),
    };
};

const mapDispachToProps = dispatch => {
    return { 
        sendResponse: (value) => dispatch({ type: SEND_RESPONSE, value }),
    };
};

export default connect(
    mapStateToProps,
    mapDispachToProps
)(ComponentAuth);